.contact-label {
	font-size: 20px;
}

.contact-form-container {
	padding: 15px;
	margin: auto;
	border: solid black 2px;
	border-radius: 20px;
	width: 400px;
	height: 275px;
	margin: 15px auto;
}

.contact-form-element {
	justify-content: center;
}

.contact-form-element-textarea {
	margin-top: 7px;
	height: 200px;
}

.input-contact-message {
	width: 75%;
	height: 50%;
}

.input-contact-name,
.input-contact-email,
.input-contact-subject {
	width: 75%;
	border-radius: 5px;
	border: 1px black solid;
	padding: 2px;
}

.contact-form-label {
	font-size: 14px;
}

textarea {
	font-family: Open Sans;
	resize: none;
	border-radius: 10px;
}

.btn {
	color: black;
	border: black 1px solid;
	background-color: white;
	border-radius: 5px;
}

.contact-form-submit {
	transition: 0.3s;
}

.contact-form-submit:hover {
	background-color: lightgrey;
}

.contact-form-element {
	margin-bottom: 5px;
}