.project-wrapper {
	text-align: center;
	border: 3px solid black;
	border-radius: 30px;
	height: 130px;
	width: 225px;
	padding: 3px;
	/* background-color: #1da1f2; */
	transition: 0.7s;
}

.project-wrapper:hover {
	/* opacity: 0.7; */
	background-color: #1da1f2;
}
.projects-information {
	margin: auto;
	width: 700px;
	font-size: 14px;
	margin-top: 15px;
	margin-bottom: 15px;
}
.projects-wrapper {
	padding: 0 10% 0 10%;
	text-align: center;
	margin: 0 auto;
	display: flex;
	align-items: center;
	justify-content: center;
}

.project-title {
	margin: 5px;
	font-size: 20px;
	text-decoration: underline;
	color: black;
	margin-bottom: 10px;
}

.project-title a,
.project-link {
	color: black;
	text-decoration: none;
	font-weight: 700;
}

.project-description {
	font-size: 12px;
	margin: 5px;
}
