/** @format */
@import 'Projects.css';
@import 'Popup.css';
@import 'Contact.css';

html {
	scroll-behavior: smooth;
	background-color: #F1F1F1;
	color: #23120b;
	widows: 110%;
	/* background-image: url("../Components/Icons/Background.jpg") ;
	background-repeat: no-repeat;
	background-size:contain; */
}

.App {
	/* margin-top: 600px; */
	widows: 110%;
	text-align: center;
	font-family: Open Sans;
	background-color: #F1F1F1;
	
}

h2 {
	font-size: 24px;
}

.main-title {
	margin: 5px;
	font-weight: 900;
	font-size: 54px;
}

.nav-container {
	display: flex;
	flex-direction: row;
	justify-content: flex-end;
	align-items: right;
	left: 0%;
	top: 0%;
	width: 100%;
	margin: 0px;
	padding: 0;
	height: 50px;
}

.nav-container a {
	font-weight: bold;
	text-decoration: none;
	color: #23120b;
}

.nav-container a:hover {
	color: darkgrey;
}

.nav-container ul {
	display: flex;
	margin: 0;
	padding: 0;
}

.nav-container li {
	list-style: none;
	padding: 1em;
}

.flex-container {
	display: flex;
	align-items: center;
	justify-content: center;
	flex-direction: row;
	margin: 5px;
	flex-wrap: wrap;
}

.flex-element {
	margin: 5px;
}

.flex-popup {
	margin: 5px;
}

.flex-break {
	flex-basis: 100%;
	height: 0;
}

.aboutMe-wrapper {
	width: 75%;
	text-align: center;
	margin: auto;
	display: flex;
}

.aboutMe-paragraph {
	text-align: center;
	padding: 12px 0 0 0;
}

.photo-me {
	height: 150px;
	border-radius: 50%;
	text-align: center;
	margin:15px;
}

.icon-image {
	height: 20px;
	opacity: 0.7;
	transition: 0.3s;
}

.contact-form-label {
	margin: auto;
}

.contact-form-label-wrapper {
	margin: auto;
	text-align: center;
	width: 75%;
}

.icon-image:hover {
	opacity: 1;
}

.header-title {
	margin: 5px;
	text-decoration: underline;
	font-size: 30px;
	color: #23120b;
}

.skill-image {
	height: 12.5vh;
}

.skill-wrapper {
	margin: auto;
	width: 80%;
}

.resume-link {
	color: #23120b;
	text-decoration: none;
}

.resume-link:visited {
	color: #23120b;
	text-decoration: none;
}

.resume-link:hover {
	color: grey;
	text-decoration: underline;
}
.inline-link{
	color: #007bff;
    text-decoration: none;
}
.inline-link:hover{
	color: #007bff;
    text-decoration: underline;
}
.separator{
	height: 0px;
	width: 85%;
	border: #939393 solid 1px;
	margin: 20px auto;
}
.education-container{
	margin: 10px 0 20px 0;
}
.education-header{
	padding-left: 10%;
	margin: 10px 0;
	font-size: 20px;
	text-align: left;
}
.education-description{
	font-size: 18px;
	font-style: italic;
	text-align: left;
}
.education-list-header{
	text-align: left;
	padding-left: 15%;
}
.education-list-element{
	text-align: left;
	padding-left: 20%;
	padding-right:10%;
	margin: 10px 0;
}
strong{
	font-weight: 1000;
	/* color: #f05454; */
}